<template>
  <div class="p-4">
    <div class="overscroll-auto">
      <table class="table-fixed border-blue-900 border-2 overscroll-auto">
        <thead>
          <tr>
            <th class="w-1/12 text-center">№</th>
            <th class="w-1/6 text-left">Name</th>
            <th class="w-1/6 text-left">Mail</th>
            <th class="w-1/12 text-center">Date</th>
            <th class="w-1/2 text-left">Messages</th>
          </tr>
        </thead>
        <tbody v-for="(message, index) in items" :key="message.id">
          <tr class="bg-blue-200 border-blue-900 border-2">
            <td class="text-center">{{ index + 1 }}</td>
            <td class="text-left">{{ message.name }}</td>
            <td class="text-left">{{ message.email }}</td>
            <td class="text-center">{{ message.date }}</td>
            <td class="text-left">{{ message.message }}</td>
          </tr>
        </tbody>
      </table>
      <paginate
        v-model="page"
        :page-count="pageCount"
        :click-handler="pageChangeHandler"
        :prev-text="'Prev'"
        :next-text="'Next'"
        :container-class="'flex pl-0 list-none rounded my-2'"
        :page-class="
          'relative block py-2 px-3 leading-tight bg-white border border-gray-300 text-blue-700 border-r-0 hover:bg-gray-200'
        "
        :prev-class="
          'relative block py-2 px-3 leading-tight bg-white border border-gray-300 text-blue-700 border-r-0 ml-0 rounded-l hover:bg-gray-200'
        "
        :next-class="
          'relative block py-2 px-3 leading-tight bg-white border border-gray-300 text-blue-700 rounded-r hover:bg-gray-200'
        "
      >
      </paginate>
    </div>
  </div>
</template>
<script>
import paginationMixin from "../mixins/pagination.mixins";
export default {
  name: "Messages",
  mixins: [paginationMixin],
  data: () => ({
    messages: [],
  }),
  async mounted() {
    this.messages = await this.$store.dispatch("fetchMessage");
    this.setupPagination(
      this.messages.map((record) => {
        return {
          ...record,
        };
      })
    );
    console.log(this.sortedItems());
  },
  methods: {},
  computed: {},
};
</script>
