<script>
import Footer from '../components/Footer'

import Navbar from '../components/Navbar'
import Header from '../components/Header'
import emailjs from 'emailjs-com';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import { required, minLength } from 'vuelidate/lib/validators'

export default {
  
  name: 'Home',
  data: () =>({
    info: [],
    categories: [],
    isActive: null,
    name: '',
    email: '',
    message: '',
    visible: false,
    animate: true,
    product: [],
    isLoading: true,
    fullPage: false,
    header: [],
    home: [],
    date: new Date().toISOString().slice(0,10) 

  }),
  async mounted () {
    this.home = await this.$store.dispatch('fetchHome')
    this.info = await this.$store.dispatch('fetchInfo')
    this.header = await this.$store.dispatch('fetchHeader')
    this.categories = this.info.first.categories
    this.isLoading = false
    console.log(this.home)
  },
  validations: {
    name: {
      required
    },
    email: {
      required,
    },
    message: {
      required,
      minLength: minLength(6)
    },
  },
  methods: {
    updateInfo(info, image, index) {
      this.info.first.info = info
      this.info.first.image = image
      this.isActive = index
    },
    async sendEmail() {
      if(this.$v.$invalid) {
        this.$v.$touch()
        return
      }
      const formData = {
        name: this.name,
        email: this.email,
        message: this.message,
        date: this.date
      }
      const userId = process.env.VUE_APP_EMAILJS_USER_ID
      try {
        emailjs.send('service_91ia0fl', 'template_7yflqww', formData, userId,)
        await this.$store.dispatch('sendMessage', formData)

      } catch (error) {
        console.log({error})
      }
      this.name = ''
      this.email = ''
      this.message = ''
    },
  },
  components: {
    Navbar,
    Header,
    Loading,
    Footer
  }
}
</script>
